//devices upto 900px
@include media-md {
  header {
    .toggle {
      display: block;
      margin-right: 230%;
      .close {
        @include positions(absolute, 20px, none, 40%, none, 50px, 50px);
        @include width(40px, 40px, $white, $primary, 50%, 8px);
        z-index: 999999;
      }
    }
    .center {
      ul {
        display: none;
      }

      .mobile-nav {
        display: block;
        @include positions(absolute, 0, none, -50px, none, none, none);
        @include width(400px, 100vh, $light, $white, 0, 0);
        padding: $large 0;
        z-index: 99999;

        li {
          margin-top: $xl;
          a {
            color: $dark;
            margin-bottom: $md;
            font-size: $xl-2;
          }
        }
      }
    }
    .right_user,
    .right_search {
      display: none;
    }
  }

  .hero {
    @include width(90%, auto, 0, 0, 0, 0);
    @include pa_ma(20% 0 0 0, none);
    &::after {
      width: 100%;
    }
    h1 {
      font-size: $xl;
      span {
        font-size: $xl-3;
      }
    }
    .search {
      @include width(100%, none, $white, $dark, 40px, 10px);
      input {
        width: 70%;
      }
    }
  }

  // card
  .cards {
    @include grid(repeat(3, 1fr), 30px);
  }
  .topproduct,
  .product {
    .product_items {
      @include grid(repeat(2, 1fr), 30px);
    }
  }
  .topproduct {
    .head {
      @include flex(center, center, column);
    }
    .category {
      @include flex(center, space-between, row);
      width: 100%;
      button {
        margin: 20px;
      }
    }
  }
  .banner {
    .posts {
      @include grid(repeat(1, 1fr), 30px);
    }
  }
  .price {
    .content {
      @include grid(repeat(1, 1fr), 30px);
      place-items: center;
    }
  }
  .customer {
    .content {
      @include grid(repeat(2, 1fr), 30px);
    }
  }
  .blog {
    .posts {
      @include grid(repeat(1, 1fr), 30px);
      place-items: center;
    }
  }
}

//devices upto 512px
@include media-sm {
  header {
    .left {
      display: none;
    }
    .right {
      .showCart {
        background: $white;
        @include positions(absolute, 11%, none, none, 20px, 350px, auto);
        @include shadow(rgba(0, 0, 0, 0.15) 0px 2px 8px);
        @include pa_ma(20px 0, 0);
        border-radius: 5px;
        z-index: 999;

        .empty {
          @include flex(center, space-between, center);
          @include pa_ma(0 20px, 0);
          img {
            width: 40px;
            height: 40px;
          }
        }
      }
      .details {
        &_content {
          &_img {
            img {
              @include width(60px, 60px, red, none, 40%, none);
              object-fit: cover;
            }
          }
        }
      }
    }
  }
  .topproduct,
  .product {
    .product_items {
      @include grid(repeat(1, 1fr), 30px);
    }
  }
  .searchItems {
    padding: 0;
    .product_items {
      @include grid(repeat(1, 1fr), 30px);
      margin-top: -25px;
      background: $white;
      border-radius: 0 0 30px 30px;
      width: 100%;
      .box {
        padding: 20px;
      }
    }
  }

  .customer {
    .content {
      @include grid(repeat(1, 1fr), 30px);
    }
  }
  .blog {
    .posts {
      @include grid(repeat(1, 1fr), 30px);
      place-items: center;
    }
  }
}
